.loader {
  width: 68px;
  height: 60px;
  background: #fff;
  border-radius: 15% 15% 35% 35%;
  margin-top: 30px;
  display: inline-block;
  position: relative;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 16px;
  height: 20px;
  border: 4px solid #fff;
  border-radius: 0 4px 4px 0;
  position: absolute;
  top: 12px;
  left: 65px;
}

.loader:before {
  content: "";
  width: 2px;
  height: 12px;
  color: #fff;
  box-sizing: border-box;
  animation: 1s infinite animloader;
  position: absolute;
  top: -18px;
  left: 22px;
}

@keyframes animloader {
  0% {
    box-shadow: 2px 0 #fff0, 12px 0 #ffffff4d, 20px 0 #fff0;
  }

  50% {
    box-shadow: 2px -5px #ffffff80, 12px -3px #ffffff80, 20px -2px #fff9;
  }

  100% {
    box-shadow: 2px -8px #fff0, 12px -5px #fff0, 20px -5px #fff0;
  }
}

.loader-wrapper {
  height: 100vh;
  width: 100vw;
  z-index: 10000;
  visibility: hidden;
  background-image: linear-gradient(225deg, #000000f0, #00000089);
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.loader-wrapper.show {
  visibility: visible;
}

